.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.home__productRow {
  display: flex;
  z-index: 1;
  margin: 0 5px 0;
}

.home__imageCarousel {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
}

.home__image {
  width: 100%;
  object-fit: contain;
  z-index: -1;
  /* margin-bottom: -150px; */
